<template>
    <div class="w">
        <Title :title="MesReleaseTitle" />
        <MesPreview title="1" preview="1" @click.native="goNJFUViewerMes(0, '1')"/>
        <MesPreview title="2" preview="2" />
        <MesPreview title="3" preview="3" />
        <MesPreview title="4" preview="4" />
        <MesPreview title="5" preview="5" />
    </div>
</template>

<script>
import Title from '../components/Title.vue';
import MesPreview from '../components/MesPreview.vue';
export default {
    name: 'FJFUMesRelease',
    components: { Title, MesPreview },
    props: ['MesReleaseID', 'MesReleaseTitle'],
    methods: {
        goNJFUViewerMes(mesID, mesTitle) {
            this.$router.push({name: 'ViewerMes', query: {mesID, mesTitle}})
        }
    }
}
</script>