<template>
    <!--  -->
    <div id="fileShow">
        <Title :title="CourseResourcesTitle" />
        <div class="file_navigation">
            <div class="w">
                <ul class="file_navigation_ul">
                    <li class="fl">
                        <FileShow title="" @click.native="goNJFUViewerPDF(0, '')" />
                    </li>
                    <li class="fl">
                        <FileShow title="" @click.native="goNJFUViewerPDF(1, '')" />
                    </li>
                    <li class="fl">
                        <FileShow title="" @click.native="goNJFUViewerPDF(2, '')" />
                    </li>
                    <li class="fl">
                        <FileShow title="" @click.native="goNJFUViewerVideo(3, '')" />
                    </li>
                    <li class="fl">
                        <FileShow title="" @click.native="goNJFUViewerPDF(4, '')" />
                    </li>
                    <li class="fl">
                        <FileShow title="" @click.native="goNJFUViewerPDF(5, '')" />
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import FileShow from '../components/FileShow.vue'
import Title from '../components/Title.vue'
export default {
    name: 'NJFUCourseResources',
    components: { FileShow, Title },
    props: ['CourseResourcesID', 'CourseResourcesTitle'],
    methods: {
        goNJFUViewerPDF(PDFID, PDFTitle) {
            this.$router.push({ name: 'ViewerPDF', query: { PDFID, PDFTitle } }, _ => { })
        },
        goNJFUViewerVideo(videoID, videoTitle) {
            this.$router.push({ name: 'ViewerVideo', query: { videoID, videoTitle } }, _ => { })
        }
    }
}
</script>

<style scoped>
.file_navigation {
    min-height: 350px;
    overflow: auto;
    padding-bottom: 20px;
}

.file_navigation_ul li {
    margin-top: 20px;
    margin-right: 40px;
}

.file_navigation_ul li:nth-child(6n) {
